import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue')
  },
  {
    path: '/campaigns/venta',
    name: 'VentaCampaigns',
    component: () => import('../components/VentaCampaigns.vue')
  },
  {
    path: '/campaigns/proceso',
    name: 'ProcesoCampaigns',
    component: () => import('../components/ProcesoCampaigns.vue')
  },
  {
    path: '/maps',
    name: 'Map',
    component: () => import('../components/Map.vue')
  },
  {
    path: '/inventario',
    name: 'Inventario',
    component: () => import('../components/Inventario.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
